.result {
    margin: auto;
    max-width: fit-content;
    transition: 1s;
}

.result__text {
    margin: 0;
    color: hsl(0, 0%, 100%);
    padding: 10px;
    font-size: 20px;
    background-color: hsl(203, 92%, 50%);
    border-radius: 5px;
}

.result:hover {
    transform: scale(1.25);
}